import React, { useState } from "react"
import styled from "styled-components"
import * as emailjs from "emailjs-com";

import Button from "./Button"

const FormWrapper = styled.form`
  width: 60%;
  margin: 0 20%;
  padding: 2rem;
  background: #444;

  @media (max-width: 700px) {
    width: 90%;
    margin: 0 5%;
  }
`

const Input = styled.input`
  display: block;
  width: 60%;
  margin: 0.5rem auto 2rem;
  padding: 1rem 2rem;
  border: 3px solid white;
  background: transparent;

  @media (max-width: 700px) {
    width: 100%;
    padding: 1rem;
  }
`

const TextArea = styled.textarea`
  display: block;
  width: 60%;
  height: 8rem;
  margin: 0.5rem auto 2rem;
  padding: 1rem 2rem;
  border: 3px solid white;
  background: transparent;

  @media (max-width: 700px) {
    width: 100%;
    padding: 1rem;
  }
`

const Label = styled.label`
  display: block;
  width: 60%;
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: 300;
  font-size: 1rem;
  letter-spacing: 0.1rem;
  line-height: 1.5rem;
  color: white;
`

const SubmitButton = styled(Button)`
  margin: 2rem auto;
`

const ContactForm = () => {
  const [formState, setFormState] = useState({
    name: "",
    message: "",
    email: "",
    sent: false,
    buttonText: "Send Message",
  })

  const resetForm = () => {
    setFormState({
      name: "",
      message: "",
      email: "",
      sent: true,
      buttonText: "Message Sent",
    })
  }

  const handleInputChange = (event) => {
    setFormState({
      ...formState,
      [event.target.name] : event.target.value
    })
  }

  const formSubmit = e => {
    const serviceID = 'default_service';
    const templateID = 'template_94rkkps';
    const USER_ID = "user_BpIe6SorAZKaTWEsyi0jy";

    e.preventDefault()

    setFormState({
      buttonText: "...sending",
    })

    let data = {
      from_name: formState.name,
      to_name: formState.email,
      message: formState.message
    }

    emailjs.send(serviceID, templateID, data, USER_ID)
    .then(function (response) {
      console.log(response.status, response.text)
      resetForm()
    }, function(err) {
      console.log(err)
    })
  }

  return (
    <FormWrapper className="contact-form" onSubmit={e => formSubmit(e)}>
      <Label className="message-name" htmlFor="message-name">
        Your Name
      </Label>
      <Input
        onChange={handleInputChange}
        name="name"
        className="message-name"
        type="text"
        placeholder="Your Name"
        value={formState.name}
      />

      <Label className="message-email" htmlFor="message-email">
        Your Email
      </Label>
      <Input
        onChange={handleInputChange}
        name="email"
        className="message-email"
        type="email"
        placeholder="your@email.com"
        value={formState.email}
        required
      />
      <Label className="message" htmlFor="message-input">
        Your Message
      </Label>
      <TextArea
        onChange={handleInputChange}
        name="message"
        className="message-input"
        type="text"
        placeholder="Please write your message here"
        value={formState.message}
        required
      />
      <SubmitButton block type="submit">
        {formState.buttonText}
      </SubmitButton>
    </FormWrapper>
  )
}

export default ContactForm
