import React, { useState, useEffect } from "react"
import styled from "styled-components"

import { useWindowSize } from "../hooks/useWindowResize"
import { sortEveryNth } from "../logic/sortEveryNth"

import PageHeader from "../components/PageHeader"
import GalleryThumbnail from "../components/GalleryThumbnail"
import Button from "../components/Button"

const BackButton = styled(Button)`
  position: absolute;
  bottom: 0;
  left: 1rem;
  width: 8rem;

  @media (max-width: 1360px) {
    position: relative;
    left: 0;
    display: block;
    margin: 0 auto 1rem;
  }
`

const GalleryWrapper = styled.main`
  width: 100%;
  min-height: 100%;
  height: auto;
  padding: 1rem;
  column-count: 4;
  column-gap: 1em;

  @media (max-width: 1365px) {
    column-count: 3;
  }
  @media (max-width: 1023px) {
    column-count: 2;
  }
  @media (max-width: 799px) {
    column-count: 1;
  }
`

const GalleryImage = styled(GalleryThumbnail)`
  width: 100%;
  height: auto;
  overflow: hidden;
`

const GalleryHairStyling = () => {
  // ***** state *****
  const [width] = useWindowSize()
  const [isLoading, setIsLoading] = useState(true)
  const [imageData, setImageData] = useState([])
  const [sortedImageData, setSortedImageData] = useState([])

  // ***** fetch data *****
  const fetchData = async url => {
    try {
      const response = await fetch(url)
      const data = await response.json()
      console.log(data.media)
      setImageData(imageData => [...imageData, ...data.media])
    } catch (err) {
      console.log("Error:", err)
    }
  }

  useEffect(() => {
    fetchData(
      "https://api.cosmicjs.com/v1/carla-hairdressing-hairdessing/media?folder=hair&pretty=true&read_key=LHBjVPuttEE2rFqHF6YANNNI6zPMCXqn9dLDZMKaA1itwgn7bd&limit=20&props=imgix_url,name,"
    )
  }, [])

  // ***** sort array of data based on viewport width *****
  // ***** not 100% sure this is sorting perfectly *****
  useEffect(() => {
    if (width > 1366) {
      setSortedImageData(sortEveryNth(imageData, 4))
    } else if (width > 1024 && width <= 1366) {
      setSortedImageData(sortEveryNth(imageData, 3))
    } else if (width > 800 && width <= 1024) {
      setSortedImageData(sortEveryNth(imageData, 2))
    } else {
      setSortedImageData(imageData)
    }
    setIsLoading(false)
  }, [width, imageData])

  return (
    <>
      <PageHeader
        title="Hair Styling"
        introduction="This is my Hair Styling collection, you can find my collaboration developed over the years for the perfection of hairstyles."
      >
        <BackButton to="/gallery" icon={"back"}>
          Go Back
        </BackButton>
      </PageHeader>
      <GalleryWrapper>
        {isLoading ? (
          <p>Loading...</p>
        ) : (
          sortedImageData.map((img, index) => (
            <GalleryImage
              key={img.name}
              src={`${img.imgix_url}?auto=format,compress&w=1000&q=75`}
              index={index.toString()}
            />
          ))
        )}
      </GalleryWrapper>
    </>
  )
}

export default GalleryHairStyling
